<!-- 概览修改 -->
<template>
  <!-- 生产进度展示与管理 -->
  <el-dialog
    :title='dialogTitle'
    center
    append-to-body
    @close='dialogClose'
    :close-on-click-modal='false'
    :close-on-press-escape='false'
    :visible.sync='visible'>
    <el-form
      ref="form"
      :rules='rules'
      :model='form'
      label-width="140px">
      <!-- prop为验证的规则名 -->
      <fieldset>
        <legend>基本信息</legend>
        <el-form-item label="订单号">
          <el-input v-model='form.orderNo'></el-input>
        </el-form-item>
        <el-form-item label="产品名称">
          <el-input v-model='form.productName'></el-input>
        </el-form-item>
      </fieldset>
      <fieldset>
        <legend>回交期</legend>
        <el-form-item label="计划开始日期">
          <el-date-picker
            v-model='form.scheduleInfo[0].planDate'
            type="date"
            placeholder="请选择日期"
            :picker-options='pickerOptions'>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="实际开始日期">
          <el-date-picker
            v-model='form.scheduleInfo[0].factDate'
            type="date"
            placeholder="请选择日期"
            :picker-options='pickerOptions'>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="备注说明">
          <el-input
            type="textarea"
            v-model='form.scheduleInfo[0].remarks'>
          </el-input>
        </el-form-item>
      </fieldset>
      <fieldset>
        <legend>领料</legend>
        <el-form-item label="计划开始日期">
          <el-date-picker
            v-model='form.scheduleInfo[1].planDate'
            type="date"
            placeholder="请选择日期"
            :picker-options='pickerOptions'>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="实际开始日期">
          <el-date-picker
            v-model='form.scheduleInfo[1].factDate'
            type="date"
            placeholder="请选择日期"
            :picker-options='pickerOptions'>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="备注说明">
          <el-input
            type="textarea"
            v-model='form.scheduleInfo[1].remarks'>
          </el-input>
        </el-form-item>
      </fieldset>
      <fieldset>
        <legend>排计划</legend>
        <el-form-item label="计划开始日期">
          <el-date-picker
            v-model='form.scheduleInfo[2].planDate'
            type="date"
            placeholder="请选择日期"
            :picker-options='pickerOptions'>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="实际开始日期">
          <el-date-picker
            v-model='form.scheduleInfo[2].factDate'
            type="date"
            placeholder="请选择日期"
            :picker-options='pickerOptions'>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="备注说明">
          <el-input
            type="textarea"
            v-model='form.scheduleInfo[2].remarks'>
          </el-input>
        </el-form-item>
      </fieldset>
      <fieldset>
        <legend>生产</legend>
        <el-form-item label="计划开始日期">
          <el-date-picker
            v-model='form.scheduleInfo[3].planDate'
            type="date"
            placeholder="请选择日期"
            :picker-options='pickerOptions'>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="实际开始日期">
          <el-date-picker
            v-model='form.scheduleInfo[3].factDate'
            type="date"
            placeholder="请选择日期"
            :picker-options='pickerOptions'>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="备注说明">
          <el-input
            type="textarea"
            v-model='form.scheduleInfo[3].remarks'>
          </el-input>
        </el-form-item>
      </fieldset>
      <fieldset>
        <legend>交货</legend>
        <el-form-item label="计划开始日期">
          <el-date-picker
            v-model='form.scheduleInfo[4].planDate'
            type="date"
            placeholder="请选择日期"
            :picker-options='pickerOptions'>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="实际开始日期">
          <el-date-picker
            v-model='form.scheduleInfo[4].factDate'
            type="date"
            placeholder="请选择日期"
            :picker-options='pickerOptions'>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="备注说明">
          <el-input
            type="textarea"
            v-model='form.scheduleInfo[4].remarks'>
          </el-input>
        </el-form-item>
      </fieldset>
    </el-form>
    <!-- 按钮 -->
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click='confirmEdit'>确认修改</el-button>
      <el-button @click='cancelEdit'>取消修改</el-button>
    </div>
  </el-dialog>
</template>
<script>
  import { rules } from '@/common/js/validateRules';
  import { uploadScheduleInfo } from '@/api/schedule';

  export default {
    data() {
      return {
        dialogTitle: '订单进度编辑',
        visible: false,
        form: {
          id: '',
          orderInformationId: '',
          orderNo: '',
          productName: '',
          scheduleInfo: [
            {
              name: '回交期',
              planDate: null,
              factDate: null,
              remarks: ''
            },
            {
              name: '领料',
              planDate: null,
              factDate: null,
              remarks: ''
            },
            {
              name: '排计划',
              planDate: null,
              factDate: null,
              remarks: ''
            },
            {
              name: '生产',
              planDate: null,
              factDate: null,
              remarks: ''
            },
            {
              name: '交货',
              planDate: null,
              factDate: null,
              remarks: ''
            }
          ]
        },
        rules: {},
        // 日期选择器选项
        pickerOptions: {
          shortcuts: [{
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '明天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周后',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        }
      }
    },
    computed: {
      dialogVisible() {
        return this.$store.getters.scheduleDialogVisible;
      },
      defaultInfo() {
        return this.$store.getters.scheduleEditDefault;
      }
    },
    watch: {
      // 观测store中存储的dialogVisible
      dialogVisible: {
        handler(val, oldVal) {
          if (this.visible !== val) {
            this.visible = val;
          }
        },
        immediate: true
      },
      // 观测store中存储的default
      defaultInfo: {
        handler(val, oldVal) {
          // this.form = val;
          if (val) {
            // id值
            this.form.id = val.id;
            // 订单号
            this.form.orderNo = val.orderNo;
            // 产品名称
            this.form.productName = val.productName;
            // 订单信息使用的id
            this.form.orderInformationId = val.orderInformationId;
            // 订单进度信息
            // for (let i = 0; i < val.scheduleInfo.length; i++) {
            //   this.form.scheduleInfo.splice(i, 1, {...val.scheduleInfo[i]})
            // }
            this.form.scheduleInfo = [...val.scheduleInfo];
          }
        }
      }
    },
    methods: {
      cancelEdit() {
        this.$store.dispatch('schedule/closeEditDialog');
      },
      // 弹窗关闭回调
      dialogClose() {
        this.$store.dispatch('schedule/closeEditDialog');
      },
      confirmEdit() {
        // 使用FormData手动序列化
        const formData = new FormData(this.$refs.form);

        // formData.append('avatar', this.file);
        // 调用api
        uploadScheduleInfo(formData, {}).then(res => {
          // 如果修改数据成功
          // 修改页面数据
          // 关闭弹窗并提示修改成功
          this.$store.dispatch('schedule/updateInfo', {...this.form});
          this.$store.dispatch('schedule/closeEditDialog');

          this.$message({
            type: 'success',
            message: '修改数据成功！！！'
          });
        }, err => {
          this.$notify({
            type: 'error',
            message: '修改数据失败，请重试！！！'
          });
          // console.log(err);
          console.log("后台返回数据有误！");
        });
      }
    },
    mounted() {
      this.$nextTick(function() {
        this.rules = rules;
      });
      window.test = this
    }
  }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
fieldset {
  border: 1px dotted #ccc;
}
</style>



