export default {
  // 数组去重
  unique: function unique(arr) {
      let result = [],
          obj = {},
          len = arr.length,
          i, key;

      for (i = 0; i < len; i++) {
        key = arr[i];

        // 如果key第一次出现，则存入result
        if (!obj[key]) {
            result.push(key);
            obj[key] = true;
        }
      }

      return result;
  },
  // 根据原始数据: arr = [{},...]
  // 获得所有元素的指定属性的值的非重复集合
  // 结果一般用作筛选按钮候选值或者自动完成候选值
  // data：对象数组
  // columns：需要的过滤操作的键值
  // result: 返回结果，result = {key: [...], ...}
  getFilters: function getUniqueColumnValues(data, columns) {
    let obj = {},
        result = {};

    // 简单的对传入参数做一下判断
    if (data && data.length && columns && columns.length) {

      // 遍历data中每一条数据的每一个属性
      data.foreach(function(item) {

        // 遍历columns
        // 取到columns的元素对应的非重复数据
        columns.foreach(function(val) {

          // 第一次遍历到该键值时
          // 创建对应项
          if (!obj[val]) {
            obj[val] = {};
            result[val] = [];
          }

          // 如果第一次遍历到就存入结果中
          if (!obj[val][item[val]]) {
            obj[val][item[val]] = true;
            result[val].push(item[val]);
          }
        });
      });
    }

    return result;
  },
  // 根据订单进度信息，计算出当前生产状态
  // 并同时获得备注信息
  getState: function getState(scheduleInfo) {
    // 订单未处理的默认备注
    const defaultState = {
      state: '未处理',
      name: '处理订单',
      planDate: new Date(),
      factDate: null,
      remarks: '订单已接收，但还未进行处理！！'
    };
    let stateInfo = {
      state: '',
      name: '',
      planDate: null,
      factDate: null,
      remarks: ''
    };

    // 如果该节点的factDate已经赋值，则认为该节点已经开始
    switch(true) {
      case !!scheduleInfo[4].factDate:
        stateInfo = {
          state: '开始交货',
          name: scheduleInfo[4].name,
          planDate: scheduleInfo[4].planDate,
          factDate: scheduleInfo[4].factDate,
          remarks: scheduleInfo[4].remarks
        }
        break;
      case !!scheduleInfo[3].factDate:
        stateInfo = {
          state: '开始生产',
          name: scheduleInfo[3].name,
          planDate: scheduleInfo[3].planDate,
          factDate: scheduleInfo[3].factDate,
          remarks: scheduleInfo[3].remarks
        }
        break;
      case !!scheduleInfo[2].factDate:
        stateInfo = {
          state: '安排计划',
          name: scheduleInfo[2].name,
          planDate: scheduleInfo[2].planDate,
          factDate: scheduleInfo[2].factDate,
          remarks: scheduleInfo[2].remarks
        }
        break;
      case !!scheduleInfo[1].factDate:
        stateInfo = {
          state: '开始领料',
          name: scheduleInfo[1].name,
          planDate: scheduleInfo[1].planDate,
          factDate: scheduleInfo[1].factDate,
          remarks: scheduleInfo[1].remarks
        }
        break;
      case !!scheduleInfo[0].factDate:
        stateInfo = {
          state: '沟通交期',
          name: scheduleInfo[0].name,
          planDate: scheduleInfo[0].planDate,
          factDate: scheduleInfo[0].factDate,
          remarks: scheduleInfo[0].remarks
        }
        break;
      default:
        stateInfo = defaultState;
        break;
    }

    return stateInfo;
  },
  // 格式化输出日期，支持自定义分隔符
  formatDate: function formatDate(date, separator = ['-', '-', '']) {
    let string = '';
    // date不为空、null、undefined时才进行格式化，
    // 否则返回空字符串
    if (date) {
      const oDate = new Date(date);
      string = oDate.getFullYear() + separator[0] +
                (oDate.getMonth() + 1) + separator[1] +
                oDate.getDate() + separator[2];
    }

    return string;
  },
  // 返回对象的的‘类’
  classof: function classof(o) {
    if (o === null) return "null";//对null和undefined进行特殊处理，在ECMAScript 5中不需要进行处理
    if (o === undefined) return "undefined";
    return Object.prototype.toString.call(o).slice(8,-1);
  },
  // 对象深拷贝
  deepClone: function deepClone(obj) {
    const oClass = this.classof(obj);
    let result;

    // 首先确定result的类型
    if (oClass === "Object") {
        result = {};
    } else if (oClass === "Array") {
        result = [];
    // 否则为简单值，直接返回
    } else {
        return obj;
    }

    for (let key in obj) {
        let copy = obj[key];
        if (this.classof(copy) === "Object") {
            result[key] = this.deepClone(copy); // 递归调用
        } else if (this.classof(copy) === "Array"){
            result[key] = this.deepClone(copy);
        } else {
            result[key] = obj[key];
        }
    }

    return result;
  }
};




