<!-- 概览展示 -->
<template>
  <!-- 生产进度展示与管理 -->
  <div class="fenix-schedule-info">
    <div>
      <el-input placeholder="请先选择搜索类型" v-model="searchForm.value" class="input-with-select">
          <!-- filterable -->
          <!-- @change="changeKeyValue" -->
        <el-select
          v-model="searchForm.valueType"
          class="my-select-type"
          slot="prepend"
          placeholder="请先选择搜索类型">
          <el-option
            v-for="valueType in searchForm.valueTypeArr"
            :key="valueType.value"
            :label="valueType.label"
            :value="valueType.value">
          </el-option>
        </el-select>
        <el-button slot="append" icon="el-icon-search"></el-button>
      </el-input>
    </div>
    <el-table
      :data='showInfos'
      stripe
      border
      style="width: 100%">
      <el-table-column
        type="index"
        width="30">
      </el-table-column>
      <el-table-column
        label="订单号"
        sortable
        prop="orderNo"
        width="180">
      </el-table-column>
      <el-table-column
        label="产品名称"
        sortable
        prop="productName"
        width="180">
      </el-table-column>
      <el-table-column
        label="当前状态"
        prop="state"
        :filters='stateFilters'
        :filter-method='stateFilter'
        width="180">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>计划{{ scope.row.name }}时间: {{ scope.row.planDate }}</p>
            <p>实际{{ scope.row.name }}时间: {{ scope.row.factDate }}</p>
            <p>备注: {{ scope.row.remarks }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag
                close-transition
                :color='stateColor(scope.row.state)'
                size="medium">
                {{ scope.row.state }}
              </el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            v-if='canEdit'
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
            size="mini"
            type="info"
            @click="showDetail(scope.$index, scope.row)">详情</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
  import UTIL from '@/common/js/util';
  import { getScheduleInfos } from '@/api/schedule';

  export default {
    data() {
      return {
        // 订单进度概览信息列表
        // sourceData: [],
        // 搜索框需要的数据
        searchForm: {
          isSearch: false, // 是否执行搜索
          valueType: 'orderNo',
          valueTypeArr: [  // 搜索类型的列表，对象数组
            {value: 'orderNo', label: '订单号'},
            {value: 'productName', label: '产品名称'},
          ],
          value: '', // 输入框的值
          searchValue: '' // 实际执行搜索的值
        },
      }
    },
    computed: {
      // 将scheduleInfo转化为需要的数据格式
      showInfos() {
        const data = this.$store.getters.scheduleSourceData;

        return data.map(function(item) {
          const stateInfo = UTIL.getState(item.scheduleInfo);

          const showInfo = {
              id: item.id,
              orderInformationId: item.orderInformationId,
              orderNo: item.orderNo,
              productName: item.productName,
              state: stateInfo.state,
              name: stateInfo.name,
              planDate: UTIL.formatDate(stateInfo.planDate, ['年', '月', '日']),
              factDate: UTIL.formatDate(stateInfo.factDate, ['年', '月', '日']),
              remarks: stateInfo.remarks
            };

          return showInfo;
        });
      },
      // state标签的筛选列表选项数组
      stateFilters() {
        const data = this.$store.getters.selectionGetSelections(['state'])['state'];
        let result = [];

        if (data && data.map) {
          result = data.map((item) => {
            return {
              text: item,
              value: item
            }
          });
        }

        return result;
      },
      // 是否可以编辑该页面
      canEdit() {
        return true;
      }
    },
    methods: {
      // 获取原始数据
      getSourceDate() {
        // 调用api
        return getScheduleInfos().then(res => {
          const scheduleInfos = res.data.scheduleInfos;
          // 如果后台返回数据成功
          // 设置sourceData
          // 否则使用提示框提示出错
          if (scheduleInfos.length) {
            this.$store.dispatch('schedule/setSourceData', scheduleInfos);
          } else {
            this.$notify({
              type: 'error',
              message: '后台返回数据出错，请联系管理员'
            });
          }
        }, err => {
          // console.log(err);
          console.log("后台返回数据有误！");
        });
      },
      // 设置筛选的可选项列表
      setFilterList() {
        const payload = {
          data: this.showInfos,
          keys: ['state']
        }

        this.$store.dispatch('selections/addSelections', payload);
      },
      // state标签筛选函数
      stateFilter(val, row) {
        return row.state === val;
      },
      // state标签的颜色
      // 此处使用hsl表示法
      stateColor(state) {
        // state全部的候选值
        // 顺序特定，方便计算颜色
        const states = ['未处理', '沟通交期', '开始领料', '安排计划', '开始生产', '开始交货'];
        // state在候选值池中的位置
        const index = states.indexOf(state);
        // state标签的颜色饱和度
        const saturation = index/(states.length - 1) * 100 + '%';
        // 生成的hsl颜色
        const color = `hsl(120, ${saturation}, 50%)`;

        return color;
      },
      handleEdit(index, row) {
        this.$store.dispatch('schedule/openEditDialog', index);
      },
      showDetail(index, row) {
        // 将搜索的id传入store中，避免在路由地址中发送参数
        // 然后跳转路由
        const params = {
          orderId: row.orderInformationId
        }

        this.$store.dispatch('schedule/getOrderInfos', params).then((res) => {
        }, err => {
          this.$notify({
            type: 'error',
            message: '请求详情失败，请重试或者联系管理员...'
          })
        });

        const detail = { name: 'scheduleSearch' };
        this.$router.push(detail);
      }
    },
    mounted() {
      this.$nextTick(function() {
        // 获取数据源
        this.getSourceDate().then(() => {
          // 将备选数据添加进入vuex
          this.setFilterList();
        });
      });
    }
  }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .fenix-schedule-info .el-tag {
    color: #fff;
  }
</style>



